import React, { Component } from 'react';
import './Work.css';
import WorkComponant from './WorkComponant/WorkComponant';
import { 
  webcomponantprops1,
  webcomponantprops2,
  webcomponantprops3,
    
    } from './WorkProps';


class Web extends Component {
  render() {
    return (
      <div className="ProjectsContainer"> 
          
          <div className="WorkCard"><WorkComponant title={webcomponantprops1}/></div>
          <div className="WorkCard"><WorkComponant title={webcomponantprops2}/></div>
          <div className="WorkCard"><WorkComponant title={webcomponantprops3}/></div >




      </div>
    );
  }
}

export default Web;