import React, { Component } from 'react';
import './Footer.css';
import FullLogoOnWhite from '../../Images/Jason-Neff-onWhite.svg'

import EmailIcon from '@mui/icons-material/Email';
import GitHubIcon from '@mui/icons-material/GitHub';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import LinkedInIcon from '@mui/icons-material/LinkedIn';

class Footer extends Component {
  render() {
    return (
      <div className="Footer" id="Contact"> 
          <img className="FooterLogo" src={FullLogoOnWhite} ></img>    

          {/* <h2 className="Header">Contact Me</h2> */}
          <div  className="HeroContact">
        <div className="ContactLink" ><EmailIcon fontSize="large" /> </div>
        <div className="ContactLink"><GitHubIcon fontSize="large"/></div>
        <div className="ContactLink"><LinkedInIcon fontSize="large"/></div>
        <div className="ContactLink"><PhoneAndroidIcon fontSize="large"/></div>
        </div>

      </div>
    );
  }
}

export default Footer;