import React, { Component } from 'react';
import './WorkComponant.css';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';
class WorkComponant extends Component {
  render() {
    return (
      <div className="WorkComponant"> 
          <div className="Preview">Test</div>
          
          <div className="WorkTitleContainer">
          <div className="WorkArrow"><ArrowDropUpIcon/></div> 
          <div className="WorkTitle">{this.props.title}</div> 
             
           </div>
     

      </div>
    );
  }
}

export default WorkComponant;