import React, { Component } from 'react';
import './Work.css';
import WorkComponant from './WorkComponant/WorkComponant';
import { 

  designcomponantprops1,
  designcomponantprops2,
  designcomponantprops3,
    
    } from './WorkProps';


class Design extends Component {
  render() {
    return (
      <div className="ProjectsContainer"> 
          
          <div className="WorkCard"><WorkComponant title={designcomponantprops1}/></div>
          <div className="WorkCard"><WorkComponant title={designcomponantprops2}/></div>
          <div className="WorkCard"><WorkComponant title={designcomponantprops3}/></div >




      </div>
    );
  }
}

export default Design;